.ml6 {
    position: relative;
    font-weight: 900;
    font-size: 3.3em;
}
  
.ml6 .text-wrapper {
    position: relative;
    display: inline-block;
    padding-top: 0.2em;
    padding-right: 0.05em;
    padding-bottom: 0.1em;
    overflow: hidden;
}  
.ml6 .letter {
    display: inline-block;
    line-height: 1em;
}

.info{
    max-width: 40rem;
    font-weight: 400;
    text-align: left;
    margin-top: 0;
    font-size: 5rem;
    line-height: 5.5rem;
}