.barra-info{
    margin-top: 0;
    
    @include Telefono{
        display: flex;
        flex-direction: column-reverse;
        align-items: center;
        margin-top: 3rem;
        .descripcion{
            display: none;
        }
        .iconos .enlaces{
            margin-top: 5rem;
        }
    }
    @include Tableta{
        .descripcion{
            display: flex;
        }
        display: grid;
        grid-template-columns: 1fr 2fr 1fr;
        align-items: end;
    }
    .iconos{
        .enlaces{
            display: flex;
            align-items: flex-end;
            justify-content:left;
            a i{
                color: $Blanco;
                margin-right: 2rem;
                font-size: 3.5rem;
                padding-bottom: 5rem;
                &:hover{
                    color: $Naranja;
                }
            }
        }
    }
    .descripcion{
        color: $Blanco;
        font-weight: 400;
        backdrop-filter: blur(0.8rem);
        .padding-info{
            padding: 1rem 3rem;
        }
        .resumen{
            text-align: justify;
            line-height: 2rem;
            font-size: 1.3rem;
            font-weight: 400;
        }
    }
    .carrusel{
        background-color: $Naranja;
        border-radius: 5px;
        .info-carrusel{
            padding: 1rem 2rem;
            text-align: center;
            //height: 28rem;
        }
        p{
            color: $Blanco;
            font-size: 3rem;
            font-weight: bold;
        }
        .flechas{
            display: flex;
            justify-content: center;
            align-items: center;
            margin: 0 auto;
            padding-top: 5rem;
            
            .flecha-izquierda{
                background-image: url(../../../build/img/Iconos/Trazado2.png);
                width: 5rem;
                height: 8rem;
                background-repeat: no-repeat;
                margin-right: 10rem;
            }
            .flecha-derecha{
                background-image: url(../../../build/img/Iconos/Trazado1.png);
                width: 5rem;
                height: 8rem;
                background-repeat: no-repeat;
            }
            
        }
    }
}
.opacidad{
    opacity: 0.5;
}
.cursor{
    cursor: pointer;
}