.hero{
    background-image: url(../img/sean-pollock-PhYq704ffdA-unsplash.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    height: auto;
    background-position: top center;
}
.degradado{
    background: rgb(3,3,3);
    background: linear-gradient(90deg, rgba(3,3,3,0.7511379551820728) 0%, rgba(36,36,36,0.7679446778711485) 60%, rgba(86,86,86,1) 100%);
    height: 100%;
}
.separar-info{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.header{
    @include Telefono{
        display: flex;
        flex-direction: column;
        align-items: center;

        .navegacion a{
            display: block;
            margin-bottom: 1rem;
        }

        .hero{
            height: auto;
        }
    }
    @include Tableta{
        padding: 3rem 0;
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        flex-direction: row;

        .navegacion{
            display: flex;
            flex-direction: row;
            align-items: center;
        }
    }
    .logo h3{
        color: $Blanco;
        font-weight: 400;
    }
    .navegacion{
        .buscador{
            width: 4.5rem;
            height: 4rem;
            cursor: pointer;
            margin-right: 1.5rem;
        }
        .menu-hamburguesa{
            width: 4.5rem;
            height: 3rem;
            background-repeat: no-repeat;
            cursor: pointer;
        }
    }
}
.contenido{
    @include Telefono{
        .contenido-info{
            display: flex;
            flex-direction: column;
            align-items: center;
        }
        .marcador-pagina > div{
            display: none;
        }
    }
    @include Tableta{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        .contenido-info{
            display: flex;
            flex-direction: column;
            align-items: flex-start;
        }
        .marcador-pagina > div{
            display: flex;
        }
    }
    h1{
        color: $Blanco;
    }
    .boton-amarillo{
        display: inline-block;
        background-color: $Naranja;
        padding: 1.5rem 1rem;
        color: $Blanco;
        font-size: 2rem;
        text-transform: uppercase;
        border-radius: 3px;

        &:hover{
            background-color: darken($Naranja, 8);
        }
    }

    .marcador-pagina{
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        align-items:flex-end;
        & > div{
            cursor: pointer;
            width: 1rem;
            height: 3.5rem;
            background-color: $Gris;
            margin-bottom: 2rem;
            
            &.seleccionado{
                background-color: $Naranja;
            }
            &:last-child{
                margin-bottom: 5rem;
            }
        }   
    }
}
.titulo{
    text-transform: uppercase;
    font-weight: 400;
}
