html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  line-height: 1.8;
}

a {
  text-decoration: none;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.seccion {
  margin-top: 5rem/2;
  margin-bottom: 5rem/2;
}

.contenido-centrado {
  max-width: 880px;
}

.no-margin {
  margin: 0;
}

.no-paddding {
  padding: 0;
}

.hero {
  background-image: url(../img/sean-pollock-PhYq704ffdA-unsplash.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  height: auto;
  background-position: top center;
}

.degradado {
  background: #030303;
  background: linear-gradient(90deg, rgba(3, 3, 3, 0.7511379552) 0%, rgba(36, 36, 36, 0.7679446779) 60%, #565656 100%);
  height: 100%;
}

.separar-info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

@media (min-width: 280px) {
  .header {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .header .navegacion a {
    display: block;
    margin-bottom: 1rem;
  }
  .header .hero {
    height: auto;
  }
}
@media (min-width: 768px) {
  .header {
    padding: 3rem 0;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    flex-direction: row;
  }
  .header .navegacion {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
}
.header .logo h3 {
  color: #fff;
  font-weight: 400;
}
.header .navegacion .buscador {
  width: 4.5rem;
  height: 4rem;
  cursor: pointer;
  margin-right: 1.5rem;
}
.header .navegacion .menu-hamburguesa {
  width: 4.5rem;
  height: 3rem;
  background-repeat: no-repeat;
  cursor: pointer;
}

@media (min-width: 280px) {
  .contenido .contenido-info {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .contenido .marcador-pagina > div {
    display: none;
  }
}
@media (min-width: 768px) {
  .contenido {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
  .contenido .contenido-info {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  .contenido .marcador-pagina > div {
    display: flex;
  }
}
.contenido h1 {
  color: #fff;
}
.contenido .boton-amarillo {
  display: inline-block;
  background-color: #F18912;
  padding: 1.5rem 1rem;
  color: #fff;
  font-size: 2rem;
  text-transform: uppercase;
  border-radius: 3px;
}
.contenido .boton-amarillo:hover {
  background-color: #ce740c;
}
.contenido .marcador-pagina {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;
}
.contenido .marcador-pagina > div {
  cursor: pointer;
  width: 1rem;
  height: 3.5rem;
  background-color: #636161;
  margin-bottom: 2rem;
}
.contenido .marcador-pagina > div.seleccionado {
  background-color: #F18912;
}
.contenido .marcador-pagina > div:last-child {
  margin-bottom: 5rem;
}

.titulo {
  text-transform: uppercase;
  font-weight: 400;
}

.ml6 {
  position: relative;
  font-weight: 900;
  font-size: 3.3em;
}

.ml6 .text-wrapper {
  position: relative;
  display: inline-block;
  padding-top: 0.2em;
  padding-right: 0.05em;
  padding-bottom: 0.1em;
  overflow: hidden;
}

.ml6 .letter {
  display: inline-block;
  line-height: 1em;
}

.info {
  max-width: 40rem;
  font-weight: 400;
  text-align: left;
  margin-top: 0;
  font-size: 5rem;
  line-height: 5.5rem;
}

.barra-info {
  margin-top: 0;
}
@media (min-width: 280px) {
  .barra-info {
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    margin-top: 3rem;
  }
  .barra-info .descripcion {
    display: none;
  }
  .barra-info .iconos .enlaces {
    margin-top: 5rem;
  }
}
@media (min-width: 768px) {
  .barra-info {
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
    align-items: end;
  }
  .barra-info .descripcion {
    display: flex;
  }
}
.barra-info .iconos .enlaces {
  display: flex;
  align-items: flex-end;
  justify-content: left;
}
.barra-info .iconos .enlaces a i {
  color: #fff;
  margin-right: 2rem;
  font-size: 3.5rem;
  padding-bottom: 5rem;
}
.barra-info .iconos .enlaces a i:hover {
  color: #F18912;
}
.barra-info .descripcion {
  color: #fff;
  font-weight: 400;
  backdrop-filter: blur(0.8rem);
}
.barra-info .descripcion .padding-info {
  padding: 1rem 3rem;
}
.barra-info .descripcion .resumen {
  text-align: justify;
  line-height: 2rem;
  font-size: 1.3rem;
  font-weight: 400;
}
.barra-info .carrusel {
  background-color: #F18912;
  border-radius: 5px;
}
.barra-info .carrusel .info-carrusel {
  padding: 1rem 2rem;
  text-align: center;
}
.barra-info .carrusel p {
  color: #fff;
  font-size: 3rem;
  font-weight: bold;
}
.barra-info .carrusel .flechas {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding-top: 5rem;
}
.barra-info .carrusel .flechas .flecha-izquierda {
  background-image: url(../../../build/img/Iconos/Trazado2.png);
  width: 5rem;
  height: 8rem;
  background-repeat: no-repeat;
  margin-right: 10rem;
}
.barra-info .carrusel .flechas .flecha-derecha {
  background-image: url(../../../build/img/Iconos/Trazado1.png);
  width: 5rem;
  height: 8rem;
  background-repeat: no-repeat;
}

.opacidad {
  opacity: 0.5;
}

.cursor {
  cursor: pointer;
}