html{
    font-size: 62.5%;
    box-sizing: border-box;
}
*,*::before, *::after{
    box-sizing: inherit;
}
body{
    font-size: 1.6rem;
    line-height: 1.8;
}
a{
    text-decoration: none;
}
h1{
    font-size: 3.8rem;
}
h2{
    font-size: 3.4rem;
}
h3{
    font-size: 3rem;
}
h4{
    font-size: 2.6rem;
}
.contenedor{
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}
.seccion{
    margin-top: 5rem / 2;
    margin-bottom: 5rem / 2;
}
.contenido-centrado{
    max-width: 880px;
}
.no-margin{
    margin: 0;
}
.no-paddding{
    padding: 0;
}